
import React, { Component } from 'react'

class Les_Biches extends Component {
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  render(){
    const source = `
    <p>	《バレエ組曲「牝鹿」<em>Les Biches Suite d’Orchestre</em>》は、1923年にフランシス・プーランクが書いたバレエ作品から彼自身が数曲を抜粋し、演奏会用組曲として編み直したものである。</p>
    <p>	《牝鹿 <em>Le Biches</em>》は彼が初めて手がけたオーケストラ作品となるのだが、12歳の頃からストラヴィンスキーのバレエ三部作に夢中だった彼にとって、それがバレエ音楽であったのは運命的だったのかもしれない。</p>
    <p>	作曲を依頼したのは、この頃、ロシアで精力的に活動していたロシア・バレエ団「バレエ・リュス」の敏腕プロデューサー、セルゲイ・ディアギレフである。彼は依頼の際、「《ラ・シルフィード》の現代版のような作品を…」と言ったそうだ。《ラ・シルフィード <em>La Sylphide</em>》とは、結婚式を控えた青年と妖精の悲恋を軸に非現実への憧れと挫折を描いた、1832年初演のバレエである。プーランクは《牝鹿》を「雰囲気のバレエ」と表現していることから、《ラ・シルフィード》における現実と幻想の間の曖昧さの再現を目指したと言えるのではないか。</p>
    <p>	「牝鹿」とはフランス語で「若い娘たち」、俗語で「かわいい子」という意味である。内容は娘たちと青年が戯れるというもので、振り付けからはあらすじの展開を読み取ることができない。そこでプーランクは音楽によって、時として聴き手をはぐらかすような意外な展開を創り出している。一瞬調性が分からなくなるような、複雑な和声により不安定感を醸し出したと思うと、また飄然と明快な旋律が戻ってくるという流れが繰り返される。そんな彼の、フランス的エスプリに満ちた音楽の特質が作品全体に盛り込まれているのだ。</p>
    <p>	バレエ音楽は本来舞踊と一体のものであり、音楽は台詞の役割を担っている。《牝鹿》はあらすじのないバレエだが、だからこそ、そこに多様な物語の可能性を与えるという点で音楽が非常に重要であるのは間違いない。その《牝鹿》が演奏会用組曲という音楽作品として独立し、舞踊という視覚的影響から自由になったとき、プーランクによる音楽はきっと素晴らしく幻想的な世界へと私たちを誘ってくれるに違いない。</p>
    <p>（甲斐朝花）</p>
`
    return(
      <div className="article concert" dangerouslySetInnerHTML={{ __html: source }} />
    )
  }
}

export default Les_Biches;
